.modal {
  background-color: white;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 200;
  padding: 5% 0;
  border-radius: 10px;
}

.modal.trippy {
  width: 60%;
}

.modal.gameNight {
  z-index: 999;
  width: 40%;
}

.modal.musico {
  z-index: 999;
