@import url('https://fonts.googleapis.com/css?family=Chivo');

div#nav .title {
  font-family: 'Chivo', sans-serif;
  letter-spacing: 3px;
}

@media only screen and (min-width: 1000px)  {
  div#nav {
    padding-left: 2%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 950;
    color: #3D2B5A;
  }

  div#nav h3 {
    padding-top: 5px;
    margin-top: 0;
    margin-bottom: 0;
    padding-bottom: 5px;
  }
}

@media only screen and (max-width: 999px)  {
  div#nav {
    /* padding-left: 2%; */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
    background-color: #3D2B5A;
    color: white;

  }

  div#nav h3.title{
    display: none;
    -ms-flex: 0 1;
        flex: 0 1;
  }

  div#nav span#navLine {
    display: none;
    -ms-flex: 0 1;
        flex: 0 1;
  }
}




a {
  color: inherit;
  text-decoration: none;
}



