@import url('https://fonts.googleapis.com/css?family=Chivo');

#blog a{
  color: black;
}

div#blog {
  margin-top: 5%;
  margin-bottom: 10%;
}

div#blog img {
  margin-top: -5%;
  padding-top: 0;
  width: 100%;
  border-bottom: 40px solid #FF9298;
}


div#blog .title {
  font-weight: 900;
  font-family: 'Chivo', sans-serif;
  font-size: 2.5em;
  letter-spacing: 5px;
  position: relative;
  z-index: 900;
  color: white;
  margin-top: -28%;
}

div#blog div#post img {
  position: relative;
  width: 95%;
  border-bottom: none;
  text-align: center;
  margin: 0 auto;
  display: block;
}

div#blog div#post h2 {
  color: #3D2B5A;
  padding-left: 6.5%;
  padding-bottom: 0;
}

#blog div#button {
  margin-top: 3%;
  margin-left: 40%;
  margin-right: 40%;
  padding: 15px;
  font-size: 1.2em;
  background-color: #3D2B5A;
  border: 1px solid #3D2B5A;
  border-radius: 8px;
}

#blog div#button {
  color: white;
}

#blog p#deets {
  color: #9F9F9F;
  padding-left: 6.5%;
}


@media only screen and (min-width: 1280px)  {
  div#blog div#post {
    margin-top: 30%;
    margin-left: 15%;
    margin-right: 15%;
    border: 2px solid #E6E6E6;
    border-radius: 6px;
    text-align: left;
  }
}

@media only screen and (max-width: 1279px)  {
  div#blog div#post {
    margin-top: 30%;
    margin-left: 20%;
    margin-right: 20%;
    border: 2px solid #E6E6E6;
    border-radius: 6px;
    text-align: left;
  }
}

@media only screen and (max-width: 830px)  {
  div#blog .title {
    margin-top: -35%;
    font-size: 2.5em;
  }
  div#blog div#post {
    margin-top: 40%;
  }

  #blog div#button {
    margin-left: 20%;
    margin-right: 20%;
  }
}

@media only screen and (max-width: 550px)  {
  div#blog .title {
    margin-top: -40%;
    font-size: 2em;
  }
  div#blog div#post {
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
  }

  #blog div#button {
    margin-left: 10%;
    margin-right: 10%;
  }
  div#blog div#smallFlex {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  div#blog1 {
    -ms-flex: 3 1;
        flex: 3 1;
    margin: 0;
    padding: 0;
  }

 div#blog div#post h2 {
    margin: 5px;;
    line-height: 1.5;
    font-size: 1em;
    padding-left: 5%;
  }

  #blog a {
    margin: 0;
  }

  #blog p#deets {
    margin: 5px;;
    line-height: 1.5;
    font-size: 1em;
    padding-left: 5%;
  }


  div#blog2 {
    -ms-flex: 1 1;
        flex: 1 1;
    margin: 0;
    padding: 0;
  }

}



